#homePageContentContainer {
    width: 100vw;
    min-height: 100vh;
    height: auto;
}

#searchBarContainer {
    text-align: center;
    position: relative;
    top: -5px;
    left: -25px;
}

#gridItemSearchBar {
    height: 60px;
}

#gridItemMainContent {
    height: auto;   
    display: grid;
    grid-template-columns: auto auto auto;
    column-gap: 20px;
    row-gap: 15px;
    padding-left: 20px;
    padding-right: 35px;
}

.createGridItems {
    background-color: white;
    height: auto;
    margin-top: 5px;
    box-shadow: 0 0 1px 1px black;
}

#searchBarInput {
    height: 38px;
    width: 300px;
    border-radius: 15px 0px 0px 15px;
    border: 1px solid black;
    font-size: 20px;
    padding-left: 10px;
}

#searchIcon {
    font-size: 40px;
    position: relative;
    top: 13px;
    border: 1px solid black;
    border-radius: 0px 15px 15px 0px;
    background-color: lightgray;
}

#searchIcon:hover {
    cursor: pointer;
    background-color: rgb(203, 203, 203);
    box-shadow: 0 0px 1px 0.10px black;
}

#filterIcon {
    font-size: 40px;
    position: relative;
    top: 11px;
}

#filterIcon:hover {
    color: rgb(80, 80, 80);
    cursor: pointer;
}

.createGridItems {
    display: grid;
    grid-template-columns: 100px auto;
    column-gap: 2px;
}

.createImage {
    width: 100px;
}

.createName {
    font-weight: bold;
    font-size: 20px;
}

.createQuestion {
    margin-top: 1px;
    text-decoration: underline;
}

@media screen and (max-width: 850px) {
    #gridItemMainContent {
        height: auto;   
        grid-template-columns: auto auto;
        column-gap: 20px;
        padding-left: 20px;
        padding-right: 35px;
    }
}

@media screen and (max-width: 850px) {
    #gridItemMainContent {
        height: auto;   
        grid-template-columns: auto;
        column-gap: 20px;
        padding-left: 20px;
        padding-right: 35px;
    }
}

@media screen and (max-width: 450px) {
    #gridItemMainContent {
        height: auto;   
        grid-template-columns: auto;
        column-gap: 20px;
        padding-left: 20px;
        padding-right: 35px;
    }

    #searchBarInput {
        height: 38px;
        width: 190px;
        border-radius: 15px 0px 0px 15px;
        border: 1px solid black;
        font-size: 20px;
        padding-left: 10px;
    }

    #searchBarContainer {
        left: -10px;
    }
}

